import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../../contexts/AuthContext';
import Button from '../../../components/ui/Button';
import Input from '../../../components/ui/Input';
import Select from '../../../components/ui/Select';
import { Eye, EyeOff, Loader2, AlertCircle } from 'lucide-react';

const AuthForm = ({ isLogin, onToggleMode, selectedLanguage }) => {
  const navigate = useNavigate();
  const { signIn, signUp, authError, clearError } = useAuth();
  
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    full_name: '',
    phone_number: '',
    role: 'patient',
    specialization: '',
    license_number: '',
    years_of_experience: '',
    confirmPassword: ''
  });
  
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [formError, setFormError] = useState('');

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Clear errors when user types
    if (formError) setFormError('');
    if (authError) clearError();
  };

  const validateForm = () => {
    if (!formData.email || !formData.password) {
      setFormError(selectedLanguage === 'hindi' ? 'à¤¸à¤­à¥€ à¤†à¤µà¤¶à¥à¤¯à¤• à¤«à¥€à¤²à¥à¤¡ à¤­à¤°à¥‡à¤‚' : 'Please fill in all required fields');
      return false;
    }

    if (!isLogin) {
      if (!formData.full_name) {
        setFormError(selectedLanguage === 'hindi' ? 'à¤ªà¥‚à¤°à¤¾ à¤¨à¤¾à¤® à¤†à¤µà¤¶à¥à¤¯à¤• à¤¹à¥ˆ' : 'Full name is required');
        return false;
      }

      if (formData.password !== formData.confirmPassword) {
        setFormError(selectedLanguage === 'hindi' ? 'à¤ªà¤¾à¤¸à¤µà¤°à¥à¤¡ à¤®à¥‡à¤² à¤¨à¤¹à¥€à¤‚ à¤–à¤¾à¤¤à¥‡' : 'Passwords do not match');
        return false;
      }

      if (formData.password.length < 6) {
        setFormError(selectedLanguage === 'hindi' ? 'à¤ªà¤¾à¤¸à¤µà¤°à¥à¤¡ à¤•à¤® à¤¸à¥‡ à¤•à¤® 6 à¤…à¤•à¥à¤·à¤° à¤•à¤¾ à¤¹à¥‹à¤¨à¤¾ à¤šà¤¾à¤¹à¤¿à¤' : 'Password must be at least 6 characters');
        return false;
      }

      if (formData.role === 'therapist' && !formData.specialization) {
        setFormError(selectedLanguage === 'hindi' ? 'à¤¥à¥‡à¤°à¥‡à¤ªà¤¿à¤¸à¥à¤Ÿ à¤•à¥‡ à¤²à¤¿à¤ à¤µà¤¿à¤¶à¥‡à¤·à¤œà¥à¤žà¤¤à¤¾ à¤†à¤µà¤¶à¥à¤¯à¤• à¤¹à¥ˆ' : 'Specialization is required for therapists');
        return false;
      }
    }

    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) return;

    setIsLoading(true);
    setFormError('');

    try {
      if (isLogin) {
        const result = await signIn(formData.email, formData.password);
        
        if (result?.success) {
          // Redirect based on user role - for now redirect to dashboard
          navigate('/patient-dashboard');
        }
      } else {
        const userData = {
          full_name: formData.full_name,
          phone_number: formData.phone_number,
          role: formData.role,
          language_preference: selectedLanguage,
          ...(formData.role === 'therapist' && {
            specialization: formData.specialization,
            license_number: formData.license_number,
            years_of_experience: formData.years_of_experience ? parseInt(formData.years_of_experience) : null
          })
        };

        const result = await signUp(formData.email, formData.password, userData);
        
        if (result?.success) {
          // Show success message or redirect
          setFormError('');
          // For email confirmation flow, show message
          alert(selectedLanguage === 'hindi' ? 'à¤–à¤¾à¤¤à¤¾ à¤¬à¤¨à¤¾à¤¯à¤¾ à¤—à¤¯à¤¾à¥¤ à¤•à¥ƒà¤ªà¤¯à¤¾ à¤…à¤ªà¤¨à¤¾ à¤ˆà¤®à¥‡à¤² à¤šà¥‡à¤• à¤•à¤°à¥‡à¤‚à¥¤' : 'Account created! Please check your email for confirmation.');
        }
      }
    } catch (error) {
      setFormError(selectedLanguage === 'hindi' ? 'à¤•à¥à¤› à¤—à¤²à¤¤ à¤¹à¥à¤† à¤¹à¥ˆ' : 'Something went wrong');
      console.log('Auth error:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const getSpecializationOptions = () => [
    { value: 'slp', label: selectedLanguage === 'hindi' ? 'à¤¸à¥à¤ªà¥€à¤š à¤²à¥ˆà¤‚à¤—à¥à¤µà¥‡à¤œ à¤ªà¥ˆà¤¥à¥‹à¤²à¥‰à¤œà¥€' : 'Speech Language Pathology' },
    { value: 'ot', label: selectedLanguage === 'hindi' ? 'à¤‘à¤•à¥à¤¯à¥‚à¤ªà¥‡à¤¶à¤¨à¤² à¤¥à¥‡à¤°à¥‡à¤ªà¥€' : 'Occupational Therapy' },
    { value: 'pt', label: selectedLanguage === 'hindi' ? 'à¤«à¤¿à¤œà¤¿à¤¯à¥‹à¤¥à¥‡à¤°à¥‡à¤ªà¥€' : 'Physiotherapy' }
  ];

  const getRoleOptions = () => [
    { value: 'patient', label: selectedLanguage === 'hindi' ? 'à¤®à¤°à¥€à¤œà¤¼' : 'Patient' },
    { value: 'family_member', label: selectedLanguage === 'hindi' ? 'à¤ªà¤¾à¤°à¤¿à¤µà¤¾à¤°à¤¿à¤• à¤¸à¤¦à¤¸à¥à¤¯' : 'Family Member' },
    { value: 'therapist', label: selectedLanguage === 'hindi' ? 'à¤¥à¥‡à¤°à¥‡à¤ªà¤¿à¤¸à¥à¤Ÿ' : 'Therapist' }
  ];

  const displayError = formError || authError;

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* Error Display */}
      {displayError && (
        <div className="flex items-center gap-2 p-3 bg-red-50 border border-red-200 rounded-md text-red-600 text-sm">
          <AlertCircle size={16} />
          <span>{displayError}</span>
        </div>
      )}

      {/* Registration Fields */}
      {!isLogin && (
        <>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Input
              name="full_name"
              type="text"
              placeholder={selectedLanguage === 'hindi' ? 'à¤ªà¥‚à¤°à¤¾ à¤¨à¤¾à¤®' : 'Full Name'}
              value={formData.full_name}
              onChange={handleInputChange}
              required={!isLogin}
              className="w-full"
            />
            <Input
              name="phone_number"
              type="tel"
              placeholder={selectedLanguage === 'hindi' ? 'à¤«à¥‹à¤¨ à¤¨à¤‚à¤¬à¤°' : 'Phone Number'}
              value={formData.phone_number}
              onChange={handleInputChange}
              className="w-full"
            />
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Select
              name="role"
              value={formData.role}
              onChange={handleInputChange}
              options={getRoleOptions()}
              placeholder={selectedLanguage === 'hindi' ? 'à¤­à¥‚à¤®à¤¿à¤•à¤¾ à¤šà¥à¤¨à¥‡à¤‚' : 'Select Role'}
              required={!isLogin}
            />
            
            {formData.role === 'therapist' && (
              <Select
                name="specialization"
                value={formData.specialization}
                onChange={handleInputChange}
                options={getSpecializationOptions()}
                placeholder={selectedLanguage === 'hindi' ? 'à¤µà¤¿à¤¶à¥‡à¤·à¤œà¥à¤žà¤¤à¤¾ à¤šà¥à¤¨à¥‡à¤‚' : 'Select Specialization'}
                required={formData.role === 'therapist'}
              />
            )}
          </div>

          {formData.role === 'therapist' && (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <Input
                name="license_number"
                type="text"
                placeholder={selectedLanguage === 'hindi' ? 'à¤²à¤¾à¤‡à¤¸à¥‡à¤‚à¤¸ à¤¨à¤‚à¤¬à¤°' : 'License Number'}
                value={formData.license_number}
                onChange={handleInputChange}
                className="w-full"
              />
              <Input
                name="years_of_experience"
                type="number"
                placeholder={selectedLanguage === 'hindi' ? 'à¤…à¤¨à¥à¤­à¤µ (à¤µà¤°à¥à¤·à¥‹à¤‚ à¤®à¥‡à¤‚)' : 'Years of Experience'}
                value={formData.years_of_experience}
                onChange={handleInputChange}
                min="0"
                className="w-full"
              />
            </div>
          )}
        </>
      )}

      {/* Email Field */}
      <Input
        name="email"
        type="email"
        placeholder={selectedLanguage === 'hindi' ? 'à¤ˆà¤®à¥‡à¤² à¤ªà¤¤à¤¾' : 'Email Address'}
        value={formData.email}
        onChange={handleInputChange}
        required
        className="w-full"
      />

      {/* Password Field */}
      <div className="relative">
        <Input
          name="password"
          type={showPassword ? 'text' : 'password'}
          placeholder={selectedLanguage === 'hindi' ? 'à¤ªà¤¾à¤¸à¤µà¤°à¥à¤¡' : 'Password'}
          value={formData.password}
          onChange={handleInputChange}
          required
          className="w-full pr-10"
        />
        <button
          type="button"
          onClick={() => setShowPassword(!showPassword)}
          className="absolute right-3 top-1/2 transform -translate-y-1/2 text-muted-foreground hover:text-foreground"
        >
          {showPassword ? <EyeOff size={20} /> : <Eye size={20} />}
        </button>
      </div>

      {/* Confirm Password Field - Registration Only */}
      {!isLogin && (
        <div className="relative">
          <Input
            name="confirmPassword"
            type={showConfirmPassword ? 'text' : 'password'}
            placeholder={selectedLanguage === 'hindi' ? 'à¤ªà¤¾à¤¸à¤µà¤°à¥à¤¡ à¤ªà¥à¤·à¥à¤Ÿà¤¿ à¤•à¤°à¥‡à¤‚' : 'Confirm Password'}
            value={formData.confirmPassword}
            onChange={handleInputChange}
            required={!isLogin}
            className="w-full pr-10"
          />
          <button
            type="button"
            onClick={() => setShowConfirmPassword(!showConfirmPassword)}
            className="absolute right-3 top-1/2 transform -translate-y-1/2 text-muted-foreground hover:text-foreground"
          >
            {showConfirmPassword ? <EyeOff size={20} /> : <Eye size={20} />}
          </button>
        </div>
      )}

      {/* Submit Button */}
      <Button
        type="submit"
        disabled={isLoading}
        className="w-full h-12 text-lg"
      >
        {isLoading ? (
          <div className="flex items-center gap-2">
            <Loader2 size={20} className="animate-spin" />
            <span>
              {selectedLanguage === 'hindi' ? 'à¤•à¥ƒà¤ªà¤¯à¤¾ à¤ªà¥à¤°à¤¤à¥€à¤•à¥à¤·à¤¾ à¤•à¤°à¥‡à¤‚...' : 'Please wait...'}
            </span>
          </div>
        ) : (
          selectedLanguage === 'hindi' ? (isLogin ? 'à¤²à¥‰à¤—à¤¿à¤¨ à¤•à¤°à¥‡à¤‚' : 'à¤–à¤¾à¤¤à¤¾ à¤¬à¤¨à¤¾à¤à¤‚') : (isLogin ? 'Sign In' : 'Create Account')
        )}
      </Button>

      {/* Toggle Mode */}
      <div className="text-center">
        <p className="text-sm text-muted-foreground">
          {selectedLanguage === 'hindi' ? (
            isLogin ? 'à¤¨à¤¯à¤¾ à¤–à¤¾à¤¤à¤¾ à¤¬à¤¨à¤¾à¤¨à¤¾ à¤¹à¥ˆ?' : 'à¤ªà¤¹à¤²à¥‡ à¤¸à¥‡ à¤–à¤¾à¤¤à¤¾ à¤¹à¥ˆ?'
          ) : (
            isLogin ? 'Need to create an account?' : 'Already have an account?'
          )}
          <button
            type="button"
            onClick={onToggleMode}
            className="ml-1 text-primary hover:text-primary/80 transition-colors font-medium"
          >
            {selectedLanguage === 'hindi' ? (
              isLogin ? 'à¤¯à¤¹à¤¾à¤ à¤¸à¤¾à¤‡à¤¨ à¤…à¤ª à¤•à¤°à¥‡à¤‚' : 'à¤¯à¤¹à¤¾à¤ à¤²à¥‰à¤—à¤¿à¤¨ à¤•à¤°à¥‡à¤‚'
            ) : (
              isLogin ? 'Sign up here' : 'Sign in here'
            )}
          </button>
        </p>
      </div>

      {/* Forgot Password - Login Only */}
      {isLogin && (
        <div className="text-center">
          <button
            type="button"
            className="text-sm text-primary hover:text-primary/80 transition-colors"
            onClick={() => {
              // TODO: Implement forgot password
              alert(selectedLanguage === 'hindi' ? 'à¤ªà¤¾à¤¸à¤µà¤°à¥à¤¡ à¤°à¥€à¤¸à¥‡à¤Ÿ à¤¸à¥à¤µà¤¿à¤§à¤¾ à¤œà¤²à¥à¤¦ à¤† à¤°à¤¹à¥€ à¤¹à¥ˆ' : 'Password reset feature coming soon');
            }}
          >
            {selectedLanguage === 'hindi' ? 'à¤ªà¤¾à¤¸à¤µà¤°à¥à¤¡ à¤­à¥‚à¤² à¤—à¤?' : 'Forgot Password?'}
          </button>
        </div>
      )}
    </form>
  );
};

export default AuthForm;